import React from 'react';
import { Breadcrumb, Button, Modal, Form, Input, Select, DatePicker } from 'antd';
import moment from 'moment';
import WordsCount from 'components/WordsCount';
import UploadFile from 'components/UploadFile';
import http from 'utils/getFetch';

const FromItem = Form.Item;
const Option = Select.Option;

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      countryList: [],
      findsUnit: 1
    }
  }

  componentDidMount() {
    const id = this.getParamsId();
    if (!id) {
      this.addInit();
    } else if (id) {
      this.modInit();
    }
  }

  //id
  getParamsId = () => {
    return this.props.match.params.id;
  }

  //
  addInit = () => {
    this.setState({
      countryList: [
        { id: 1, name: '' },
        { id: 2, name: '' },
        { id: 3, name: '' },
        { id: 4, name: '' }
      ]
    });
  }

  //
  modInit = () => {
    const id = this.getParamsId();
    console.log(id);
    this.setState({
      countryList: [
        { id: 1, name: '' },
        { id: 2, name: '' },
        { id: 3, name: '' },
        { id: 4, name: '' }
      ],
      findsUnit: 2
    });
    this.props.form.setFieldsValue({
      name: '',
      countryId: 2,
      findsAmount: '5000',
      signTime: moment('2016-03-18'),
      effectRange: [moment('2017-11-10'), moment('2019-12-10')],
      numberPics: [
        {
          fileName: ".jpg",
          fileUrl: "http://p1.pstatp.com/large/pgc-image/260341bcf19e480c8e7c15ed783e1d1d",
          mark: "260341bcf19e480c8e7c15ed783e1d1d"
        }
      ],
      numberVideos: [
        {
          fileName: ".mp4",
          fileUrl: "https://videos.36krcdn.com/20200324/v2_1585020734179_video_mp4_v3",
          mark: "260341bcf19e480c8e7c15ed783e1d1d"
        }
      ],
      remark: ''
    });
  }

  //
  handleSubmit = () => {
    const { findsUnit } = this.state;
    const id = this.getParamsId();

    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        const [effectStart, effectEnd] = values.effectRange || [];
        const data = {
          ...values,
          id,
          findsUnit: values.findsAmount ? findsUnit : '',
          signTime: values.signTime ? values.signTime.valueOf() : '',
          effectStart: effectStart ? effectStart.valueOf() : '',
          effectEnd: effectEnd ? effectEnd.valueOf() : '',
          effectRange: ''
        };

        this.setState({ loading: true });
        http.post(`sys/global/asia/${id ? 'mod' : 'add'}`, data)
        .then(() => {
          this.setState({ loading: false });
          Modal.confirm({
            content: `${id ? '' : ''}?`,
            okText: '',
            cancelText: '',
            onOk: () => {
              if (id) {
                this.props.history.goBack();
              } else {
                this.props.history.push('/home/global/asia');
              }
            }
          });
        })
        .catch(() => this.setState({ loading: false }));
      }
    });
  }

  render() {
    const { loading, countryList, findsUnit } = this.state;
    const { getFieldDecorator, getFieldsValue } = this.props.form;
    const { name = '', remark = '' } = getFieldsValue();
    const id = this.getParamsId();

    return (
      <div className="layout-flex">
        <div className="flex-header">
          <Breadcrumb>
            <Breadcrumb.Item ></Breadcrumb.Item>
            <Breadcrumb.Item ></Breadcrumb.Item>
            <Breadcrumb.Item >{id ? '' : ''}</Breadcrumb.Item>
          </Breadcrumb>
          <div>
            <Button type="primary" icon="save"
              loading={loading}
              onClick={this.handleSubmit}
            ></Button>
            <Button ghost type="primary" icon="rollback"
              disabled={loading}
              onClick={this.props.history.goBack}
            ></Button>
          </div>
        </div>
        <div className="flex-body">
          <Form labelCol={{ span: 6 }} wrapperCol={{ span: 12 }}>
            <FromItem label="">
              {getFieldDecorator('name', {
                rules: [
                  { required: true, message: '' },
                  { max: 50, message: '50' },
                ]
              })(
                <Input placeholder=""
                  addonAfter={<WordsCount current={name.length} total={50} />}
                />
              )}
            </FromItem>
            <FromItem label="/">
              {getFieldDecorator('countryId')(
                <Select placeholder="" allowClear>
                  {countryList.map(item => (
                    <Option key={item.id} value={item.id}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </FromItem>
            <FromItem label="">
              {getFieldDecorator('findsAmount')(
                <Input placeholder=""
                  addonAfter={
                    <Select placeholder="" style={{ width: 90 }}
                      value={findsUnit}
                      onChange={v => this.setState({ findsUnit: v })}
                    >
                      <Option value={1}>()</Option>
                      <Option value={2}>($)</Option>
                    </Select>
                  }
                />
              )}
            </FromItem>
            <FromItem label="">
              {getFieldDecorator('signTime')(
                <DatePicker style={{ width: '100%' }} />
              )}
            </FromItem>
            <FromItem label="">
              {getFieldDecorator('effectRange')(
                <DatePicker.RangePicker style={{ width: '100%' }} />
              )}
            </FromItem>
            <FromItem label="">
              {getFieldDecorator('numberPics')(
                <UploadFile
                  fileType="image"
                  layout="square"
                  accept="image/*,.jpg,.jpeg,.png,.gif"
                  limit={{ width: 500, height: 320 }}
                  maxLength={3}
                  allowCrop
                />
              )}
            </FromItem>
            <FromItem label="">
              {getFieldDecorator('numberVideos')(
                <UploadFile
                  fileType="video"
                  addBtnType="dragger"
                  accept="video/*"
                  limit={{ size: 1024 * 1024 }}
                  maxLength={3}
                />
              )}
            </FromItem>
            <FromItem label="" extra="100">
              {getFieldDecorator('remark', {
                rules: [
                  { max: 100, message: '100' },
                ]
              })(
                <Input.TextArea rows={6} placeholder="" />
              )}
              <WordsCount style={{ display: 'block' }} current={remark.length} total={100} />
            </FromItem>
          </Form>
        </div>
      </div>
    );
  }
}

export default Form.create({})(Page);
